import axios from './base'

const InteriorEnv = {
    Get(name) {
        switch(name) {
            case "Humidity": return axios.get(`/api/interior/humidity`)
            case "Temperature": return axios.get(`/api/interior/temperature`)
            case "Pressure": return axios.get(`/api/interior/pressure`)
        }
        
    },
}

export default InteriorEnv