<template>
  <vs-card v-show="show" id="chart">
    <div id="table">
      <vs-table max-items="10" v-model="selected" pagination :data="data">
        <template slot="header">
          <h3 style="font-size:1.1rem">
            {{name}}
          </h3>
        </template>
        <template  id="table" slot="thead">
          <vs-th>Datetime</vs-th>
          <vs-th>Sender ID</vs-th>
          <vs-th>Receiver ID</vs-th>
          <vs-th>Position</vs-th>
          <vs-th>Disturbance</vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data" >
            <vs-td>{{data[indextr].last_update}}</vs-td>
            <vs-td>{{data[indextr].sender_id}}</vs-td>
            <vs-td>{{data[indextr].receiver_id}}</vs-td>
            <vs-td>{{[data[indextr].position.X,data[indextr].position.Y,data[indextr].position.Z]}}</vs-td>
            <vs-td>{{data[indextr].disturbance}}</vs-td>
          </vs-tr>
        </template>
      </vs-table>
    </div>
  </vs-card>
</template>

<script>
export default {
  data() {
    return {
      show: 0,
      i: 40,
      data: [],
      sensors: [],
      selected: {},
      name: '',
      cTitle: '',
      selectedSensor: 0,
    }
  },
  methods: {
    draw() {
      this.$api.ExternalEnv.Get(this.name)
      .then(res=> {
        if(!res.data.flag) return
        for(var i=0;i<res.data.data.length;i++) {
          var d = Date.now()-res.data.data[i].last_update - (4 * 60 * 60000)
          res.data.data[i].last_update = new Date(d).toJSON().substr(5, 14).replace('T', ' ')
        }
        this.data = res.data.data
      })
    },
  },
  mounted() {
    this.$EventBus.$on("selected",(selected)=>{
      if(selected.path[1].name=="External Environment") setTimeout(()=>{
        this.name = selected.name
        this.draw()
        this.show = 1
      },250)
      else this.show = 0
    })
  }
}
</script>

<style lang="stylus" scoped>
#chart
  width 100%
  height 660px
</style>