<template>
  <vs-card id="eclss" v-show="show">
    <vs-table  max-items="15" stripe pagination :data="data">
      <template slot="header">
        <h3 style="font-size:1.1rem">
         {{ name }}
        </h3>
      </template>
      <template slot="thead">
        <vs-th>Unit ID</vs-th>
        <vs-th>Last Update</vs-th>
        <vs-th>Health Status</vs-th>
        <vs-th>Operation Status</vs-th>
        <vs-th>Power Consumption</vs-th>

        <template v-if="name == 'Water Recovery System'">
          <vs-th>Humidity</vs-th>
          <vs-th>Water Level</vs-th>
        </template>
        <template v-else-if="name == 'Oxygen Generation System'">
          <vs-th>O2 Level</vs-th>
          <vs-th>CO2 Level</vs-th>
        </template>
        <template v-else-if="name == 'Food Management System'">
          <vs-th>Refrigerator Temp</vs-th>
          <vs-th>Moisture</vs-th>
          <vs-th>Circulation</vs-th>
        </template>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="indextr" v-for="(tr, indextr) in data" >
          <vs-td> {{data[indextr].unit_id}} </vs-td>
          <vs-td> {{data[indextr].last_update}} </vs-td>
          <vs-td> {{data[indextr].health_status}} </vs-td>
          <vs-td> {{data[indextr].operation_status}} </vs-td>
          <vs-td> {{data[indextr].power_consumption}} </vs-td>

          <template v-if="name == 'Water Recovery System'">
            <vs-td> {{data[indextr].humidity}} </vs-td>
            <vs-td> {{data[indextr].water_level}} </vs-td>
          </template>
          <template v-else-if="name == 'Oxygen Generation System'">
            <vs-td> {{data[indextr].oxygen_level}} </vs-td>
            <vs-td> {{data[indextr].co2_level}} </vs-td>
          </template>
          <template v-else-if="name == 'Food Management System'">
            <vs-td> {{data[indextr].refrigerator_temp}} </vs-td>
            <vs-td> {{data[indextr].moisture}} </vs-td>
            <vs-td> {{data[indextr].circulation}} </vs-td>
          </template>
        </vs-tr>
      </template>
    </vs-table>
  </vs-card>
</template>

<script>

export default {
  data() {
    return {
      name: "",
      show: 0,
      data: [],
    }
  },
  methods: {
    draw() {
      this.$api.ECLSS.Get(this.name)
      .then(res=> {
        if(!res.data.flag) return
        for(var i=0;i<res.data.data.length;i++) {
          var d = Date.now()-res.data.data[i].last_update - (4 * 60 * 60000)
          res.data.data[i].last_update = new Date(d).toJSON().substr(5, 14).replace('T', ' ')
        }
        this.data = res.data.data
      })
    },
  },
  mounted() {
    this.$EventBus.$on("selected",(selected)=>{
      if(selected.path[1].name=="ECLSS") setTimeout(()=>{
        this.show = 1
        this.name = selected.name
        this.draw()
      }, 200)
      else this.show = 0
    })
  }
}
</script>

<style lang="stylus" scoped>
#eclss
  width 100%
  height 660px
</style>