import axios from './base'

const Structural = {
    Get(name) {
        switch(name) {
            case "Temperature": return axios.get(`/api/structural/temperature`)
            case "Strain": return axios.get(`/api/structural/strain`)
            case "Pressure": return axios.get(`/api/structural/pressure`)
            case "Damage Information": return axios.get(`/api/structural/damageinfo`)
            case "Acceleration": return axios.get(`/api/structural/acceleration`)
            case "Displacement": return axios.get(`/api/structural/displacement`)
            case "Visual Assessment": return axios.get(`/api/structural/visualassessment`)
        }
    },
}

export default Structural