import axios from './base'

const PowerSys = {
    Get(name) {
        switch(name) {
            case "Generation": return axios.get(`/api/power/powergeneration`)
            case "Consumption": return axios.get(`/api/power/powerconsumption`)
            case "Storage": return axios.get(`/api/power/storage`)
            case "Health": return axios.get(`/api/power/health`)

        }
    }
}

export default PowerSys