import axios from './base'

const ECLSS = {
    Get(name) {
        switch(name) {
            case "Water Recovery System": return axios.get(`/api/eclss/wrs`)
            case "Oxygen Generation System": return axios.get(`/api/eclss/ogs`)
            case "Food Management System": return axios.get(`/api/eclss/fms`)
        }
    }
}

export default ECLSS