<template>
  <vs-card id="human" v-show="show">
    <vs-table  max-items="15" stripe pagination :data="data">
      <template slot="header">
        <h3 style="font-size:1.1rem">
         {{ name }}
        </h3>
      </template>
      <template slot="thead">
        <template v-if="name == 'Human Agents'">
          <vs-th>ID</vs-th>
          <vs-th>Last Update</vs-th>
          <vs-th>Status</vs-th>
          <vs-th>Heart Rate</vs-th>
          <vs-th>Blood Pressure</vs-th>
          <vs-th>O2 Saturation</vs-th>
        </template>
        <template v-else-if="name == 'Set-Point Control Signals'">
          <vs-th>Datetime</vs-th>
          <vs-th>Sender ID</vs-th>
          <vs-th>Receiver ID</vs-th>
          <vs-th>Set-Point</vs-th>
        </template>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="indextr" v-for="(tr, indextr) in data" >
          <template v-if="name == 'Human Agents'">
            <vs-td> {{data[indextr].human_id}} </vs-td>
            <vs-td> {{data[indextr].last_update}} </vs-td>
            <vs-td> {{data[indextr].human_status}} </vs-td>
            <vs-td> {{data[indextr].heart_rate}} </vs-td>
            <vs-td> {{data[indextr].blood_pressure}} </vs-td>
            <vs-td> {{data[indextr].o2_saturation}} </vs-td>
          </template>
          <template v-else-if="name == 'Set-Point Control Signals'">
            <vs-td> {{data[indextr].last_update}} </vs-td>
            <vs-td> {{data[indextr].sender_id}} </vs-td>
            <vs-td> {{data[indextr].receiver_id}} </vs-td>
            <vs-td> {{data[indextr].set_point}} </vs-td>
          </template>
        </vs-tr>
      </template>
    </vs-table>
  </vs-card>
</template>

<script>

export default {
  data() {
    return {
      name: "",
      show: 0,
      data: [],
    }
  },
  methods: {
    draw() {
      this.$api.Human.Get(this.name)
      .then(res=> {
        if(!res.data.flag) return
        for(var i=0;i<res.data.data.length;i++) {
          var d = Date.now()-res.data.data[i].last_update - (4 * 60 * 60000)
          res.data.data[i].last_update = new Date(d).toJSON().substr(5, 14).replace('T', ' ')
        }
        this.data = res.data.data
      })
    },
  },
  mounted() {
    this.$EventBus.$on("selected",(selected)=>{
      if(selected.path[1].name=="Human") setTimeout(()=>{
        this.show = 1
        this.name = selected.name
        this.draw()
      }, 200)
      else this.show = 0
    })
  }
}
</script>

<style lang="stylus" scoped>
#human
  width 100%
  height 660px
</style>