<template>
  <vs-card v-show="show" id="chart">
    <div id="table">
      <vs-table max-items="10" v-model="selected" @selected="select" pagination :data="data">
        <template slot="header">
          <h3 style="font-size:1.1rem">
            {{name}}
          </h3>
        </template>
        <template  id="table" slot="thead">
          <vs-th>ID</vs-th>
          <vs-th>Last Update</vs-th>
          <vs-th>Status</vs-th>
          <vs-th>Power Consumption</vs-th>
          <vs-th>Position</vs-th>
          <vs-th>Value</vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data" >
            <vs-td>{{data[indextr].sensor_id}}</vs-td>
            <vs-td>{{data[indextr].last_update}}</vs-td>
            <vs-td>{{data[indextr].sensor_status}}</vs-td>
            <vs-td>{{data[indextr].power_consumption}}</vs-td>
            <vs-td>{{[data[indextr].position.X,data[indextr].position.Y,data[indextr].position.Z]}}</vs-td>
            <vs-td>{{data[indextr].value}}</vs-td>
          </vs-tr>
        </template>
      </vs-table>
    </div>

    <ECharts autoresize :options="option"/>

  </vs-card>
</template>

<script>
import ECharts from "vue-echarts/components/ECharts";
import "echarts/lib/component/title";
import "echarts/lib/chart/line";

export default {
  components: {
    ECharts
  },
  data() {
    return {
      show: 0,
      i: 40,
      data: [],
      sensors: [],
      selected: {},
      name: '',
      cTitle: '',
      selectedSensor: "632D",
      option: {
        title: {
          text: this.cTitle,
          top: -5,
          zLevel: -1,
        },
        grid: {
          top: '15%',
          left: '5%',
          right: '5%',
          bottom: '35%'
        },
        xAxis: {
          type: 'category',
          data: [],
          splitLine: {
              show: false
          }
        },
        yAxis: {
          type: 'value',
          min:0,
          max:30,
          splitLine: {
              show: false
          }
        },
        series: [{
          type: 'line',
          lineStyle: {
            width: 2.5,
            color: 'blue'
          },
          showSymbol: false,
          smooth: true,
          hoverAnimation: false,
          animation: false,
          data: this.data
        }]
      }
    }
  },
  methods: {
    drawTable() {
      this.$api.Structural.Get(this.name)
      .then(res=> {
        if(!res.data.flag) return
        for(var i=0;i<res.data.data.length;i++) {
          var d = Date.now()-res.data.data[i].last_update - (4 * 60 * 60000)
          res.data.data[i].last_update = new Date(d).toJSON().substr(5, 14).replace('T', ' ')
        }
        this.data = res.data.data
        this.selectedSensor = this.data[0].sensor_id
        this.drawChart()
      })
    },
    drawChart() {
      this.option.xAxis.data = []
      this.option.series[0].data = []
      this.option.title.text = this.cTitle

      for (var i = 0; i < this.i; i++) {
        this.option.xAxis.data.push(i)
        this.option.series[0].data.push(Math.random()*7+11)
      }
    },
    select(tr) {
      this.selectedSensor = tr.sensor_id
      this.cTitle = "Sensor - " + this.selectedSensor
      this.drawChart()
    }
  },
  mounted() {
    this.$EventBus.$on("selected",(selected)=>{
      if(selected.path[1].name=="Structural System") setTimeout(()=>{
        this.name = selected.name
        this.cTitle = "Sensor - " + this.selectedSensor
        this.drawTable()
        this.show = 1
      },250)
      else this.show = 0
    })
  }
}
</script>

<style lang="stylus" scoped>
#chart
  width 100%
  height 700px
.echarts
  margin-top 10px
  width 100%
  height 200px
  z-index 1000
</style>