<template>
  <vs-card id="eclss" v-show="show">
    <vs-table  max-items="15" stripe pagination :data="data">
      <template slot="header">
        <h3 style="font-size:1.1rem">
         {{ name }}
        </h3>
      </template>
      <template slot="thead">
        <template v-if="name == 'Health'">
          <vs-th>Operation Unit ID</vs-th>
          <vs-th>Last Update</vs-th>
          <vs-th>Health State</vs-th>
        </template>
        <template v-else-if="name == 'Storage'">
          <vs-th>Battery ID</vs-th>
          <vs-th>Last Update</vs-th>
          <vs-th>Battery Status</vs-th>
          <vs-th>Battery Efficiency</vs-th>
          <vs-th>Battery Percent</vs-th>
          <vs-th>voltage</vs-th>
        </template>
        <template v-else-if="name == 'Consumption'">
          <vs-th>Consumer ID</vs-th>
          <vs-th>Last Update</vs-th>
          <vs-th>Power Consumption</vs-th>
        </template>
        <template v-else-if="name == 'Generation'">
          <vs-th>Solar Panel ID</vs-th>
          <vs-th>Last Update</vs-th>
          <vs-th>Solar Panel Status</vs-th>
          <vs-th>Solar Panel Efficieny</vs-th>
          <vs-th>Position</vs-th>
          <vs-th>Power Generation</vs-th>
          <vs-th>Heat Generation</vs-th>
        </template>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="indextr" v-for="(tr, indextr) in data" >
          <template v-if="name == 'Health'">
            <vs-td> {{data[indextr].operation_unit_id}} </vs-td>
            <vs-td> {{data[indextr].last_update}} </vs-td>
            <vs-td> {{data[indextr].health_state}} </vs-td>
          </template>
          <template v-else-if="name == 'Storage'">
            <vs-td> {{data[indextr].battery_id}} </vs-td>
            <vs-td> {{data[indextr].last_update}} </vs-td>
            <vs-td> {{data[indextr].battery_status}} </vs-td>
            <vs-td> {{data[indextr].battery_id_efficiency}} </vs-td>
            <vs-td> {{data[indextr].battery_percent}} </vs-td>
            <vs-td> {{data[indextr].voltage}} </vs-td>
          </template>
          <template v-else-if="name == 'Consumption'">
            <vs-td> {{data[indextr].consumer_id}} </vs-td>
            <vs-td> {{data[indextr].last_update}} </vs-td>
            <vs-td> {{data[indextr].power_consumption}} </vs-td>
          </template>
          <template v-else-if="name == 'Generation'">
            <vs-td> {{data[indextr].solar_panel_id}} </vs-td>
            <vs-td> {{data[indextr].last_update}} </vs-td>
            <vs-td> {{data[indextr].solar_panel_status}} </vs-td>
            <vs-td> {{data[indextr].solar_panel_efficiency}} </vs-td>
            <vs-td>{{[data[indextr].position.X,data[indextr].position.Y,data[indextr].position.Z]}}</vs-td>
            <vs-td> {{data[indextr].power_generation}} </vs-td>
            <vs-td> {{data[indextr].heat_generation}} </vs-td>
          </template>
        </vs-tr>
      </template>
    </vs-table>
  </vs-card>
</template>

<script>

export default {
  data() {
    return {
      name: "",
      show: 0,
      data: [],
    }
  },
  methods: {
    draw() {
      this.data = []
      this.$api.PowerSys.Get(this.name)
      .then(res=> {
        if(!res.data.flag) return
        for(var i=0;i<res.data.data.length;i++) {
          var d = Date.now()-res.data.data[i].last_update - (4 * 60 * 60000)
          res.data.data[i].last_update = new Date(d).toJSON().substr(5, 14).replace('T', ' ')
        }
        this.data = res.data.data
      })
    },
  },
  mounted() {
    this.$EventBus.$on("selected",(selected)=>{
      if(selected.path[1].name=="Power System") setTimeout(()=>{
        this.show = 1
        this.name = selected.name
        this.draw()
      }, 250)
      else this.show = 0
    })
  }
}
</script>

<style lang="stylus" scoped>
#eclss
  width 100%
  height 660px
  max-height 660px
</style>